import Block1 from './Vip-Business-Block1.vue'
import Block2 from './Vip-Business-Block2.vue'
import longButton from '../../components/my-components/LongButton.vue'
export default {
    components: {
        Block1,
        Block2,
        longButton
    },
    props: {
        Jurisdiction: {
            type: Object,
            default: {},
            required: true
        },
        info: {
            type: Object,
            default: {
                info1: {
                    pay: 0,
                    bill: 0,
                    rights: 0,
                    refund: 0
                },
                info2: {
                    newBillTime: '',
                    newBrowserTime: ''
                }
            }
        }
    },
    methods: {
        checkBill() {
            if (this.Jurisdiction.order_view) {
                this.$emit('click', {
                    type: 'checkBill'
                })
            }else{
                this.Toast('暂无查看权限')
            }
        }
    },
}
